<template>
  <div class="cart">
    <div class="content layout">
      <div class="search-form">
        <el-form :model="searchForm" inline>
          <el-form-item label="商品名称">
            <el-input v-model="searchForm.key"></el-input>
          </el-form-item>
          <el-form-item
            ><el-button type="primary">搜索</el-button></el-form-item
          >
        </el-form>
      </div>
      <div class="table-div">
        <div class="table">
          <el-table
            border
            :data="tableData"
            @selection-change="handleSelectionChange"
            style="width: 100%"
          >
            <el-table-column
              type="selection"
              align="center"
              width="50px"
            ></el-table-column>
            <el-table-column prop="date" label="商品" align="center">
              <template slot-scope="{ row }">
                <div class="goods flex-center-y" @click="toDetail(row)">
                  <el-image :src="row.url" style="width: 100px"></el-image>
                  {{ row.title }}
                </div>
              </template>
            </el-table-column>
            <el-table-column
              prop="color"
              label="颜色"
              align="center"
              width="80px"
            >
            </el-table-column>
            <el-table-column
              prop="size"
              label="尺寸"
              align="center"
              width="80px"
            >
            </el-table-column>
            <el-table-column
              prop="price"
              label="单价（元）"
              align="center"
              width="100px"
            >
            </el-table-column>
            <el-table-column prop="count" label="数量" align="center">
              <template slot-scope="{ row }">
                <el-input-number
                  size="small"
                  v-model="row.count"
                  :min="1"
                ></el-input-number>
              </template>
            </el-table-column>
            <el-table-column
              prop="pay"
              label="实付款（元）"
              align="center"
              width="120px"
            >
            </el-table-column>
            <el-table-column prop="remark" label="备注" align="center">
              <template slot-scope="scope">
                <el-button type="text" @click="del(scope)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div class="needMail">
        <el-checkbox v-model="needMail">是否需要邮寄</el-checkbox>
      </div>
      <div class="result flex-right">
        <div class="goods-count">
          已选商品 <span class="primary t-big">{{ goodsCount }}</span> 件
        </div>
        <div class="goods-price">
          合计：<span class="primary t-big">¥{{ goodsPrice }}</span>
        </div>
        <el-button type="primary">生成订单</el-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      searchForm: {
        key: ''
      },
      tableData: [
        {
          id: '7037',
          gid: '7037',
          title: '彪马情侣短T(1)',
          url: require('@/assets/images/200(1).jpg'),
          color: '黑色',
          size: '5XL',
          price: '29',
          count: 1,
          pay: 29,
          state: '已确认',
          remark: ''
        }
      ],
      multipleSelection: [],
      goodsCount: 0,
      goodsPrice: 0,
      needMail: false
    }
  },
  methods: {
    del (scope) {
      console.log('删除', scope)
    },

    handleSelectionChange (val) {
      this.multipleSelection = val
    }
  }
}
</script>
<style lang="scss" scoped>
.cart {
  padding: 20px;
  .content {
    background-color: $color_bg_white;
    padding: 20px;
    .table-div {
      .table {
        .table-title {
          padding: 12px 0;
          .time,
          .number-value {
            margin-right: 20px;
          }
          .number-value {
          }
        }
        .goods {
          cursor: pointer;
          &:hover {
            color: $color_primary;
          }
        }
      }
    }
    .needMail{
      padding: 20px 0;
    }
    .result {
      line-height: 30px;
      .goods-count {
        margin-right: 20px;
      }
      .goods-price {
        margin-right: 20px;
      }
    }
  }
}
</style>
